<template>
  <div class="android">
    <div class="bg">
      <img class="title-1" src="../static/images/title3.png" alt="" />
      <img class="title-2" src="../static/images/title4.png" alt="" />
      <img
        src="../static/images/btn3.png"
        class="download"
        @click="go"
        alt=""
      />
      <div class="content"></div>
      <button
        class="fenxiang"
        v-clipboard:copy="nowurl"
        v-clipboard:success="onCopy"
      ></button>
      <img class="QRimg" src="../static/images/img3.png" alt="" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dPmub: "",
      nowurl: "",
    };
  },
  methods: {
    go() {
      window.open("https://www.pgyer.com/jK2y");
    },
    isurl() {
      // console.log(window.location.href);
      this.nowurl = window.location.href;
    },
    onCopy() {
      this.$dialog.alert({
        title: "内容已复制到剪切板！",
        message: "请分享到微信或企业微信",
        theme: "round-button",
      });
    },
  },
  mounted() {
    this.dPmub = this.$route.params.dPmub;
    if (this.dPmub) {
      window.location.hash = `#/android?myDPmub=${this.dPmub}`;
    }
    this.isurl();
  },
};
</script>
