<template>
  <div class="ios">
    <div class="bg">
      <img class="title-1" src="../static/images/title3.png" alt="" />
      <img class="title-2" src="../static/images/title4.png" alt="" />
      <button class="download" @click="go"></button>
      <div class="content">
        <p class="c-input">
          <span>兑换码：</span>
          <span>{{ exchangeCode }}</span>
        </p>
      </div>
      <button
        class="fenxiang"
        v-clipboard:copy="nowurl"
        v-clipboard:success="onCopy"
      ></button>
      <img class="QRimg" src="../static/images/img3.png" alt="" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      exchangeCode: "",
      exchangeLink: "",
      dPmub: "",
      nowurl: "",
    };
  },
  methods: {
    go() {
      window.open(this.exchangeLink);
    },
    onCopy() {
      this.$dialog.alert({
        title: "内容已复制到剪切板！",
        message: "请分享到微信或企业微信",
        theme: "round-button",
      });
    },
    isurl() {
      // console.log(window.location.href);
      this.nowurl = window.location.href;
    },
  },
  mounted() {
    this.exchangeLink = this.$route.params.exchangeLink;
    this.exchangeCode = this.$route.params.exchangeCode;
    this.dPmub = this.$route.params.dPmub;
    console.log(this.dPmub);
    if (this.dPmub) {
      window.location.hash = `#/ios?myDPmub=${this.dPmub}`;
    }
    this.isurl();
  },
};
</script>
